.capture-image-wrapper {
    background-color: rgb(40, 40, 43);

    .webcam {
        position: absolute;
        top: 0;
    }

    .mob-frame-wrapper {
        background-color: rgba(0, 0, 0);
        opacity: .9;
        mask-image: inherit;
        mask-position: inherit;
        -webkit-mask-image: linear-gradient(to bottom, rgb(0, 0, 0, .8) 25%, transparent 60%, rgb(0, 0, 0, .8) 25%),
            linear-gradient(to top, rgb(0, 0, 0, .8) 25%, transparent 65%, rgb(0, 0, 0, .8) 25%);
        -webkit-mask-position: 0px -30px;
    }

    .frame-content {
        .outer-circle {
            height: 60px;
            width: 60px;
            padding: .5rem;
            border-radius: 50%;
            border: 3px solid white;

            .circle-icon {
                font-size: 50px;
            }
        }

        .close-icon-desk {
            position: absolute;
            top: 48px;
            right: 48px;
        }

        .close-icon-mob {
            position: absolute;
            top: 20px;
            right: 20px;
        }
    }
}